import { DOCUMENT } from '@angular/common';
import {
  Component,
  Inject,
  OnInit,
  OnDestroy,
  ChangeDetectorRef,
} from '@angular/core';

import { cloneDeep } from 'lodash';
import { DialogService } from 'ng2-bootstrap-modal';
import { Observable } from 'rxjs';

import { AskForSellingStatusService } from '../../services/ask-for-selling-status.service';
import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';
import { OpenModalService } from '../../../../core/services/open-modal.service';
import { UserService } from '../../../../core/services/user.service';
import { StateManagerService } from '../../../../core/services/state-manager.service';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { InformationModalComponent } from '../../../../shared/components/information-modal/information-modal.component';
import { ComponentCanDeactivate } from '../../../../core/guards/can-deactivate.guard';
import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import { isMediumDevice } from '../../../../shared/utils/responsive.util';
import { isNullOrUndefined } from '../../../../shared/utils/null-or-undefined.util';
import { LoggerService } from '../../../../core/services/logger/logger.service';
import { LOGGER } from '../../../../core/services/logger/logger.provider';

type Tab = 'overview' | 'edit' | 'settings';

const SHOW_ALERT_DELAY: number = 10;

@Component({
  selector: 'app-my-properties',
  templateUrl: './my-properties.component.html',
  styleUrls: ['./my-properties.component.css']
})
export class MyPropertiesComponent implements OnInit, OnDestroy, ComponentCanDeactivate {
  public _name: string = 'my-properties';

  public activeSection: Tab = 'overview';
  public activeBlock: string = '';
  public activePropertyId: string = '';
  public activeProperty: ExtendedMarkerModel = null;
  public dirtyMarker: ExtendedMarkerModel = null;

  public myObjects: ExtendedMarkerModel[];

  public isLoaded: boolean = false;
  public isError: boolean = false;

  public feedbackState: boolean = true;
  private isControlsInvalid: boolean = false;
  public isPropertiesScrollContainerHidden: boolean = false;
  public isSmallMobileDevice: boolean = isSmallMobileDevice();
  public isMediumDevice: boolean = isMediumDevice();
  showSaveSuccess: boolean = false;
  showSaveError: boolean = false;

  public get isOverview(): boolean {
    return this.activeSection === 'overview';
  }

  public get isEdit(): boolean {
    return this.activeSection === 'edit';
  }

  public get isSettings(): boolean {
    return this.activeSection === 'settings';
  }

  private get isDirtyMarkerUpdated(): boolean {
    if (isNullOrUndefined(this.dirtyMarker)) {
      return false;
    }
    return !this.dirtyMarker.isEqual(this.activeProperty);
  }

  public get userRole(): string {
    return this.userService.role;
  }

  public get isOwner(): boolean {
    return this.userService.isOwnerRole;
  }

  public get rights(): string {
    let rightsResult = null;
    if (this.dirtyMarker.Owner && this.dirtyMarker.Realtor) rightsResult = 'owner_realtor';
    else if (this.dirtyMarker.Owner && !this.dirtyMarker.Realtor) rightsResult = 'owner';
    else if (!this.dirtyMarker.Owner && this.dirtyMarker.Realtor) rightsResult = 'realtor';
    return rightsResult;
  }

  private async loadProfileData(): Promise<void> {
    try {
      this.myObjects = await this.propertiesRepositoryService.getProfileData();
      if (this.myObjects.length) await this.setActiveObject(this.myObjects);
      this.isLoaded = true;
    } catch (error) {
      this.logger.error('ProfilePage::loadProfileData:Error', error);
      this.isLoaded = true;
      this.isError = true;
    }
  }

  private async setActiveObject(myProperties): Promise<void> {
    if (this.isMediumDevice && myProperties.length > 1) this.activePropertyId = null;
    else {
      this.toggleNavbar(true);
      this.activePropertyId = myProperties[0].PropertyId;
      try {
        this.activeProperty = await this.propertiesRepositoryService.getMarkerDetails(this.activePropertyId, false);
        this.dirtyMarker = cloneDeep(this.activeProperty);
      } catch (error) {
        this.logger.error('ProfilePage::setActiveObject:Error', error);
      }
    }
  }

  private toggleNavbar(isShown): void {
    this.stateManagerService.isDashboardNavbarShown = isShown;
  }

  constructor(
    private askForSellingStatusService: AskForSellingStatusService,
    private propertiesRepositoryService: PropertiesRepositoryService,
    private dialogService: DialogService,
    private openModalService: OpenModalService,
    private userService: UserService,
    private stateManagerService: StateManagerService,
    private changeDetectorRef: ChangeDetectorRef,
    @Inject(DOCUMENT) private document: Document,
    @Inject(LOGGER) private logger: LoggerService,
  ) {}

  public async ngOnInit(): Promise<void> {
    await this.loadProfileData();
  }

  public ngOnDestroy(): void {
    this.toggleNavbar(true);
  }

  public async activateTab({tab, update, block}): Promise<void>  {
    if (update) {
      await this.loadProfileData();
    }
    this.activateSection(tab, block);
  }

  public backToPropertiesList(): void {
    if (this.isDirtyMarkerUpdated) {
      this.initInformationModal().subscribe( (message) => {
        if (message) {
          this.showPropertiesList();
        } else {
          return;
        }
      });
    } else {
      this.showPropertiesList();
    }
  }

  public showPropertiesList(): void {
    this.isPropertiesScrollContainerHidden = false;
    this.activeProperty = null;
    this.dirtyMarker = null;
    this.activePropertyId = '';
    this.changeFeedbackSavingDisplay(false);
    this.toggleNavbar(true);

    // for tabs redrawing
    setTimeout(() => this.changeDetectorRef.detectChanges(), 300);
  }

  public activateSection(section: Tab, block?: string): void {
    if (this.isDirtyMarkerUpdated) {
      this.initInformationModal().subscribe( (message) => {
        if (!message) return;
        this.showSection(section, block);
      });
    } else {
      this.showSection(section, block);
    }
  }

  private showSection(section: Tab, block?: string): void {
    if (block) {
      this.activeBlock = block;
      setTimeout( () => {
        this.activeBlock = null;
      }, 500);
    }

    this.activeSection = section;
  }

  public async activateProperty(id: string): Promise<void> {
    this.activePropertyId = id;
    this.activeProperty = await this.propertiesRepositoryService.getMarkerDetails(this.activePropertyId, false);
    this.dirtyMarker = cloneDeep(this.activeProperty);
    if (this.isMediumDevice) {
      this.isPropertiesScrollContainerHidden = true;
      this.toggleNavbar(false);
      this.changeFeedbackSavingDisplay(true);
      this.document.getElementById('main').scrollIntoView();
    }
  }

  public async updateActiveProperty(): Promise<void> {
    this.myObjects = await this.propertiesRepositoryService.getProfileData();
    this.activeProperty = await this.propertiesRepositoryService.getMarkerDetails(this.activePropertyId, false);
    this.dirtyMarker = cloneDeep(this.activeProperty);
  }

  public updateDirtyProperty(dirtyMarker: ExtendedMarkerModel): void {
    this.dirtyMarker = dirtyMarker;
  }

  public changeFeedbackSavingDisplay(display: boolean): void {
    this.feedbackState = display;
  }

  public updateControlsInvalid(invalid: boolean): void {
    this.isControlsInvalid = invalid;
  }

  public initInformationModal() {
    return new Observable((observer) => {
      this.openModalService.open();
      this.dialogService.addDialog(
        InformationModalComponent,
        {
          text: 'Du hast deinen nderungen nicht gespeichert. Willst du wirklich fortfahren ohne zu speichern?',
          buttonText: 'nderungen speichern',
          buttonText2: 'Fortfahren',
          style: 'choice',
          page: this._name,
        }
      ).subscribe( async (message) => {
        this.openModalService.close();
        if (message) {
          if (message === 'true') {
            await this.saveProperty();
          } else if (message === 'false') {
            this.dirtyMarker = cloneDeep(this.activeProperty);
          }
        }
        observer.next(message);
      });
    });
  }

  public async saveProperty(): Promise<void> {
    try {
      await this.propertiesRepositoryService.updatePropertyDetail(this.activePropertyId, this.dirtyMarker);
      await this.updateActiveProperty();
      this.showSuccessAlert();
    } catch (error) {
      this.showErrorAlert();
    }
  }

  private showSuccessAlert(): void {
    this.showSaveSuccess = true;
    setTimeout(() => {
      this.showSaveSuccess = false;
    }, 1000 * SHOW_ALERT_DELAY);
  }

  private showErrorAlert(): void {
    this.showSaveError = true;
    setTimeout(() => {
      this.showSaveError = false;
    }, 1000 * SHOW_ALERT_DELAY);
  }

  public saveMarkerAndCheckSellingStatus(): void {
    const canSave = this.isDirtyMarkerUpdated && !this.isControlsInvalid;
    if (!canSave) {
      return;
    }

    this.askForSellingStatusService.askIfNecessary(this.dirtyMarker)
      .then(() => this.saveProperty(), () => this.saveProperty());
  }

  public trackPropertyById(index: number, property: ExtendedMarkerModel): string {
    return property.id;
  }

  canDeactivate(): boolean | Observable<boolean> {
    return new Observable((observer) => {
      if (this.isDirtyMarkerUpdated) {
        this.initInformationModal().subscribe( () => {
          return observer.next(true);
        });
      } else {
        return observer.next(true);
      }
    });
  }
}
