import {
  Component,
  OnInit,
  Input,
  ChangeDetectionStrategy,
  Inject,
  Output,
  EventEmitter,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { FavoriteModel } from '../../../../core/models/favorite-estate.model';
import { WINDOW } from '../../../../core/services/window.provider';
import { FeatureToggleService } from '../../../../core/services/feature-toggle/feature-toggle.service';
import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import { SHARED_ROUTES_NAMES } from '../../../../app.constants';

const SELLING_STATUS_ICON_MAP: { [ sellingStatus: string ]: string } = {
  'ForSale': 'for-sale',
  'MakeMeMove': 'make-me-move',
  'Idle': 'inactive',
  'Active': 'active',
  'NotActiveYet': 'not-active-yet',
};

@Component({
  selector: 'app-favorite-item',
  templateUrl: './favorite-item.component.html',
  styleUrls: ['./favorite-item.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FavoriteItemComponent implements OnInit {
  @Input() favorite: FavoriteModel = null;

  @Output() updateOfferPrice: EventEmitter<string> = new EventEmitter<string>();
  @Output() toggleNotification: EventEmitter<string> = new EventEmitter<string>();
  @Output() goToMap: EventEmitter<FavoriteModel> = new EventEmitter<FavoriteModel>();
  @Output() goToChat: EventEmitter<FavoriteModel> = new EventEmitter<FavoriteModel>();

  private screenResolution: number;
  private format: string;
  private prefix: string;

  public name: string = 'favorite-item';
  public isSmallMobileDevice: boolean = isSmallMobileDevice();
  public brokerPage: Array<string> = [`/${SHARED_ROUTES_NAMES.BROKER}`];

  public get getPropertyImageView(): string {
    return `../../../../../assets/images/property_view_imgs/${this.prefix}_min_${this.screenResolution}.${this.format}`;
  }

  public get iconThemeName(): string {
    return SELLING_STATUS_ICON_MAP[this.favorite.sellingStatus];
  }

  private displayConditions(): void {
    this.format = this.window['_canIUseWebP'] ? 'webp' : 'jpg';
    this.prefix = this.favorite.isPropertyOfTypeEFH ? 'SFH' : 'MFH';

    const displayWidth: number = this.document.body.clientWidth;

    if (displayWidth > 1920) this.screenResolution = 3840;
    else if (displayWidth > 1440 && displayWidth <= 1920) this.screenResolution = 1920;
    else if (displayWidth > 768 && displayWidth <= 1440) this.screenResolution = 1440;
    else if (displayWidth >= 0 && displayWidth <= 768) this.screenResolution = 768;
  }

  constructor(
    public featureToggleService: FeatureToggleService,
    @Inject(DOCUMENT) private document: Document,
    @Inject(WINDOW) private window: Window
  ) { }

  public ngOnInit(): void {
    this.displayConditions();
  }

  public goToEstate(): void {
    this.goToMap.emit(this.favorite);
  }

  public changeOfferPrice(): void {
    this.updateOfferPrice.emit(this.favorite.uuid);
  }

  public toggleNotificationSubscription(state: boolean): void {
    this.favorite.receiveNotifications = state;

    this.toggleNotification.emit(this.favorite.uuid);
  }

  public chatWithPerson(): void {
    this.goToChat.emit(this.favorite);
  }
}
