import {
  Component,
  EventEmitter,
  Inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';

import { cloneDeep, debounce } from 'lodash';
import { DialogService } from 'ng2-bootstrap-modal';
import { NgxModalStack } from 'ngx-multi-modal';
import { ScrollToService, ScrollToConfigOptions } from '@nicky-lenaers/ngx-scroll-to';

import { AskForSellingStatusService } from '../../services/ask-for-selling-status.service';
import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';
import { ExtendedMarkerModel } from '../../../../core/models/extended-marker.model';
import { InformationModalComponent } from '../../../../shared/components/information-modal/information-modal.component';
import { OpenModalService } from '../../../../core/services/open-modal.service';
import { LoggerService } from '../../../../core/services/logger/logger.service';
import { LOGGER } from '../../../../core/services/logger/logger.provider';
import { AnalyticsService } from '../../../../core/services/analytics/analytics.service';
import { FeatureToggleService } from '../../../../core/services/feature-toggle/feature-toggle.service';

interface IModalParams {
  style:       string; // 'info' | 'choice' | 'choice, attention' | 'success' | 'choice, content-top'
  text:        string;
  attention:   string;
  buttonText:  string;
  buttonText2: string;
  page:        string;
}

@Component({
  selector: 'app-property-edit',
  templateUrl: './property-edit.component.html',
  styleUrls: ['./property-edit.component.css']
})
export class PropertyEditComponent implements OnInit, OnChanges, OnDestroy {
  @Input() marker: ExtendedMarkerModel = null;
  @Input() block: any = null;
  @Input() userRole: string;
  @Input() rights: string;
  @Input() propertyPrice: number;

  @Output() updateActiveProperty: EventEmitter<void> = new EventEmitter<void>();
  @Output() updateDirtyProperty: EventEmitter<any> = new EventEmitter<any>();
  @Output() changeFeedbackSavingDisplay: EventEmitter<any> = new EventEmitter<any>();
  @Output() updateControlsInvalid: EventEmitter<boolean> = new EventEmitter<boolean>();

  private changes: Map<string, any> =  new Map<string, any>();

  public dirtyMarker: ExtendedMarkerModel = null;
  public markerIsDirty: boolean = false;

  public _name: string = 'property-edit';
  public displayMode: string = 'edit';

  public isPropertyDataShown: boolean = true;
  public isPropertyCostsShown: boolean = true;
  public isPropertyImagesShown: boolean = true;
  public isPropertyEquipmentShown: boolean = true;
  public isPropertyDescriptionShown: boolean = true;
  public isPropertyEnergyShown: boolean = true;
  public isPropertyContactShown: boolean = true;
  public isPropertyActivateInfo: boolean = false;
  public isSaveBtnInViewport: boolean = false;

  public scrollBlock: any = null;
  public myPropertiesEditSaveBtn: any = null;

  public tooltipContent: string = 'some text ...';

  public controlsValidation: any = {};
  public isControlsInvalid: boolean = false;

  public get isDirtyMarkerUpdated(): boolean {
    return JSON.stringify(this.marker) !== JSON.stringify(this.dirtyMarker);
  }

  public get priceToStr(): string {
    return this.dirtyMarker.Price.toLocaleString('de-DE') + ' ';
  }

  public get hasListing(): boolean {
    return this.dirtyMarker.Listing;
  }

  public get isOwner(): boolean {
    return this.userRole === 'owner';
  }

  public get isRealtor(): boolean {
    return this.userRole === 'realtor';
  }

  public get isOwnerRealtorRights(): boolean {
    return this.rights === 'owner_realtor';
  }

  private initControlValidation(): void {
    this.controlsValidation = {
      LastModernisationYear: false,
      ConstructionYear: false,
    };
  }

  private emitUpdateControlsInvalid(): void {
    const controlsValidationArr = Object.values(this.controlsValidation);
    const res = controlsValidationArr.some((controlInvalid) => {
      return controlInvalid === true;
    });
    this.isControlsInvalid = res;
    this.updateControlsInvalid.emit(this.isControlsInvalid);
  }

  private updateIsDirty(): void {
    this.markerIsDirty = this.changes.size > 0;
  }

  private isElementInViewport(el): boolean {
    const rect = el.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) && /*or $(window).height() */
      rect.right <= (window.innerWidth || document.documentElement.clientWidth) /*or $(window).width() */
    );
  }

  constructor(
    private modalService: NgxModalStack,
    private propertiesRepository: PropertiesRepositoryService,
    private dialogService: DialogService,
    private openModalService: OpenModalService,
    private scrollToService: ScrollToService,
    private analyticsService: AnalyticsService,
    private askForSellingStatusService: AskForSellingStatusService,
    @Inject(LOGGER) private logger: LoggerService,
    public featureToggleService: FeatureToggleService,
  ) {
    this.onScroll = debounce(this.onScroll.bind(this), 300);
  }

  public ngOnInit(): void {
    this.initControlValidation();
    this.myPropertiesEditSaveBtn = document.getElementById('myPropertiesEditSaveBtn');
    this.scrollBlock = document.getElementById('scrollBlock');
    this.scrollBlock.addEventListener('scroll', () => {
      this.onScroll();
    });
  }

  public ngOnChanges(): void {
    this.changes.clear();
    this.updateIsDirty();
    this.dirtyMarker = cloneDeep(this.marker);
    if (this.block) {
      setTimeout( () => {
        this.goBlock(this.block);
      }, 300);
    }
  }

  public ngOnDestroy(): void {
    this.scrollBlock.removeEventListener('scroll', () => {
      this.onScroll();
    });
  }

  public async uploadImage(body: FormData): Promise<void> {
    try {
      await this.propertiesRepository.uploadImage(this.dirtyMarker.id, body);
      // because backend is down after upload
      const updatedMarker = await this.propertiesRepository.reloadProperty(this.dirtyMarker);
      Object.assign(this.dirtyMarker, updatedMarker);
    } catch (error) {
      this.logger.error('PropertyEditComponent::uploadImage:', error);
    } finally {
      this.emitUpdateActiveProperty();
    }
  }

  public async removeImages(imageIds: Array<string>): Promise<void> {
    imageIds.map(async (imgId) => await this.propertiesRepository.removeImage(this.dirtyMarker.id, imgId));
    setTimeout( () => {
      this.emitUpdateActiveProperty();
    }, 100);
  }

  public async propertyHide(event: boolean): Promise<void> {
    this.resetDirtyMarker();
    this.dirtyMarker.ShowAddress = !event;
    await this.saveMarker();
  }

  public async changeProperty({key, value}): Promise<void> {
    if (key === 'Nothing') {
      this.dirtyMarker['Garden'] = false;
      this.dirtyMarker['VisitorsWC'] = false;
      this.dirtyMarker['Cellar'] = false;
      this.dirtyMarker['Balcony'] = false;
      this.dirtyMarker['Kitchen'] = false;
      this.dirtyMarker['SecondaryEntrance'] = false;
      this.dirtyMarker['AirConditioned'] = false;
      this.dirtyMarker['Elevator'] = false;
      this.dirtyMarker['Chimney'] = false;
      this.dirtyMarker['Sauna'] = false;
      this.dirtyMarker['SwimmingPool'] = false;
    } else if (key === 'ConstructionYear' || key === 'LastModernisationYear') {
      if (value) {
        this.dirtyMarker[key] = value + '-01-01';
      } else {
        this.dirtyMarker[key] = null;
      }
    } else {
      this.dirtyMarker[key] = value;
    }
    this.updateIsDirty();
    this.emitUpdateDirtyProperty();
  }

  public async changePropertyNew(key, value): Promise<void> {
    if (key === 'Nothing') {
      this.dirtyMarker['Garden'] = false;
      this.dirtyMarker['VisitorsWC'] = false;
      this.dirtyMarker['Cellar'] = false;
      this.dirtyMarker['Balcony'] = false;
      this.dirtyMarker['Kitchen'] = false;
      this.dirtyMarker['SecondaryEntrance'] = false;
      this.dirtyMarker['AirConditioned'] = false;
      this.dirtyMarker['Elevator'] = false;
      this.dirtyMarker['Chimney'] = false;
      this.dirtyMarker['Sauna'] = false;
      this.dirtyMarker['SwimmingPool'] = false;
    } else {
      this.dirtyMarker[key] = value;
    }
    this.updateIsDirty();
    this.emitUpdateDirtyProperty();
  }

  public inputed(key: string, invalid: boolean): void {
    this.controlsValidation[key] = invalid;
    this.emitUpdateControlsInvalid();
  }

  public yearStrToNum(year: string): number {
    return (year === null) ? 0 : parseInt(year, 10);
  }

  public yearNumToStr(year: number): string {
    return (year === 0) ? null : year + '-01-01';
  }

  public onScroll(): void {
    const isSaveBtnInViewportNew = this.isElementInViewport(this.myPropertiesEditSaveBtn);
    if (this.isSaveBtnInViewport === isSaveBtnInViewportNew) return;
    this.isSaveBtnInViewport = isSaveBtnInViewportNew;
    this.emitChangeFeedbackSavingDisplay(!this.isSaveBtnInViewport);
  }

  public goBlock(block: string): void {
    const config: ScrollToConfigOptions = {
      target: block,
      duration: 650,
      easing: 'easeOutElastic',
      offset: 20
    };

    this.scrollToService.scrollTo(config);
  }

  public toggleDataSection(section: string): void {
    switch (section) {
      case 'costsData':
        this.isPropertyCostsShown = !this.isPropertyCostsShown;
        break;
      case 'detailData':
        this.isPropertyDataShown = !this.isPropertyDataShown;
        break;
      case 'imagesData':
        this.isPropertyImagesShown = !this.isPropertyImagesShown;
        break;
      case 'equipmentData':
        this.isPropertyEquipmentShown = !this.isPropertyEquipmentShown;
        break;
      case 'descriptionData':
        this.isPropertyDescriptionShown = !this.isPropertyDescriptionShown;
        break;
      case 'energyData':
        this.isPropertyEnergyShown = !this.isPropertyEnergyShown;
        break;
      case 'contactData':
        this.isPropertyContactShown = !this.isPropertyContactShown;
        break;
    }
  }

  public async initInformationModal(params: IModalParams): Promise<any> {
    return new Promise((resolve, reject) => {
      this.openModalService.open();
      this.dialogService.addDialog(
        InformationModalComponent,
        params
      ).subscribe( (message) => {
        this.openModalService.close();
        resolve(message);
      });
    });
  }

  public async switcherClick(event): Promise<void> {
    event.preventDefault();
    event.stopPropagation();
    await this.confirmPropertyChange();
  }

  public async confirmPropertyChange(): Promise<void> {
    let isConfirm = null;
    let params: IModalParams;

    if (!this.hasListing) {
      params = {
        text: 'Mchtest du die Anzeige wirklich aktivieren?',
        attention: '',
        buttonText: 'Anzeige aktivieren',
        buttonText2: 'Abbrechen',
        style: 'choice, content-top',
        page: this._name,
      };
      isConfirm = await this.initInformationModal(params);
    } else {
      params = {
        text: 'Willst du die Anzeige endgltig lschen?',
        attention: 'Achtung, dieser Schritt kann nicht mehr rckgngig gemacht werden.',
        buttonText: 'Lschen',
        buttonText2: 'Abbrechen',
        style: 'choice, attention',
        page: this._name,
      };
      isConfirm = await this.initInformationModal(params);
    }

    if (isConfirm === 'true') {
      this.switchListing();
      if (this.hasListing) {
        await this.createrListing(this.dirtyMarker.EstimatedPrice);
        params = {
          text: 'Deine Anzeige ist jetzt verffentlicht!',
          attention: '',
          buttonText: 'Fortfahren',
          buttonText2: '',
          style: 'success',
          page: this._name,
        };
        await this.initInformationModal(params);
      } else {
        await this.deleteListing();
      }
      this.emitUpdateActiveProperty();
    }
  }

  public switchListing(): void {
    this.dirtyMarker.Listing = !this.dirtyMarker.Listing;
  }

  public resetDirtyMarker(): void {
    this.dirtyMarker = cloneDeep(this.marker);
  }

  public async deleteListing(): Promise<void> {
    await this.propertiesRepository.deleteForSaleMarkersList(this.dirtyMarker.id);
  }

  public async createrListing(salePrice): Promise<void> {
    await this.propertiesRepository.createForSaleMarkersList(this.dirtyMarker.id, {salePrice});
  }

  public saveMarkerAndCheckSellingStatus(): void {
    this.askForSellingStatusService.askIfNecessary(this.dirtyMarker)
      .then(() => this.saveMarker(), () => this.saveMarker());
  }

  public emitUpdateActiveProperty(): void {
    this.updateActiveProperty.emit();
  }

  public emitUpdateDirtyProperty(): void {
    this.analyticsService.triggerFormSubmissionSuccess('editPropertyForm', 'edit-property-from-profile');
    this.updateDirtyProperty.emit(this.dirtyMarker);
  }

  public emitChangeFeedbackSavingDisplay(display: boolean): void {
    this.changeFeedbackSavingDisplay.emit(display);
  }

  private async saveMarker(): Promise<void> {
    await this.propertiesRepository.updatePropertyDetail(this.dirtyMarker.id, this.dirtyMarker);
    this.emitUpdateActiveProperty();
  }
}
