import { Component, Inject } from '@angular/core';

import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

import { OpenModalService } from '../../../../core/services/open-modal.service';
import { isSmallMobileDevice } from '../../../../shared/utils/responsive.util';
import { PropertiesRepositoryService } from '../../../../core/services/properties-repository.service';
import { LoggerService } from '../../../../core/services/logger/logger.service';
import { LOGGER } from '../../../../core/services/logger/logger.provider';
import { AnalyticsService } from '../../../../core/services/analytics/analytics.service';

interface NotificationSettingModel {
  notification: string;
  alertId:      string;
}

@Component({
  selector: 'app-notification-settings',
  templateUrl: './notification-settings.component.html',
  styleUrls: ['./notification-settings.component.css'],
})

export class NotificationSettingComponent
  extends DialogComponent <NotificationSettingModel, string>
  implements NotificationSettingModel {

  public notification: string = '';
  public alertId: string = '';

  public _name: string = 'notification-settings';

  public get isSmallMobileDevice(): boolean {
    return isSmallMobileDevice();
  }

  private async _changeNotifPeriod(): Promise<any> {
    const payload = { emailFrequency: this.notification };
    try {
      this.propertiesRepository.updateSearchAlert(this.alertId, payload);
    } catch (error) {
      this.logger.error('NotificationSettings:_changeNotifPeriod:Error', error);
    }
  }

  constructor(
    public dialogService: DialogService,
    private openModalService: OpenModalService,
    private propertiesRepository: PropertiesRepositoryService,
    private analyticsService: AnalyticsService,
    @Inject(LOGGER) private logger: LoggerService,
  ) {
    super(dialogService);
  }

  public apply(): void {
    this._changeNotifPeriod();

    this.analyticsService.triggerFormSubmissionSuccess('notificationSettingsForm', 'search-alerts');

    this.openModalService.close();
    this.result = this.notification;
    this.close();
  }

  public close(): void {
    super.close();
  }
}
