import { Component } from '@angular/core';

import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

import { isSmallDevice } from '../../../../shared/utils/responsive.util';
import { OpenModalService } from '../../../../core/services/open-modal.service';
import { AnalyticsService } from '../../../../core/services/analytics/analytics.service';

interface DeleteFavoriteModel {
  title: string;
}

interface MessageModel {
  message: string;
  dontAskAgain: boolean;
}

@Component({
  selector: 'app-delete-favorite-estate',
  templateUrl: './delete-favorite-estate.component.html',
  styleUrls: ['./delete-favorite-estate.component.css'],
})
export class DeleteFavoriteEstateComponent
  extends DialogComponent <DeleteFavoriteModel, MessageModel>
  implements DeleteFavoriteModel {

  public title: string = '';
  public dontAskAgain: boolean = false;

  public _name: string = 'delete-favorite-estate';

  public isSmallDevice: boolean = isSmallDevice();

  constructor(
    public dialogService: DialogService,
    private openModalService: OpenModalService,
    private analyticsService: AnalyticsService,
  ) {
    super(dialogService);
  }

  /** @TODO:
   * Delete current estate from favorite list.
   */
  public apply(): void {
    // @FIXME: later review for adding logic

    this.analyticsService.triggerFormSubmissionSuccess('deleteFavoriteEstateForm', 'profile-page');
    this.openModalService.close();
    this.result = {
      message: 'delete',
      dontAskAgain: this.dontAskAgain
    };
    this.close();
  }

  public hideModal(): void {
    this.close();
  }
}
